import pageView from '@/components/common-ui/page-view.vue';
import { deepClone } from '@/until/index';
import rightBox from '@/components/common-components/right-box.vue';
import { v4 as uuidv4 } from 'uuid';
let businessManagement = class businessManagement extends Vue {
    constructor() {
        super(...arguments);
        this.className = '标签管理';
        this.showBtn = false;
        this.saveState = false;
        this.oprateConfig = [{
                text: '添加',
                action: 'add'
            }];
        this.tagList = [];
    }
    tagListChange() {
        this.saveState = true;
    }
    commonTagListChange(val) {
        if (this.tagList !== val) {
            this.tagList = deepClone(val);
        }
    }
    get schoolInfo() {
        return this.$store.state.schoolInfo;
    }
    get commonTagList() {
        return this.$store.state.schoolInfo.tagList || [];
    }
    addTag() {
        let pushObj = {
            id: uuidv4(),
            name: ''
        };
        this.tagList.push(pushObj);
