    deleteTag(index) {
        this.tagList.splice(index, 1);
    }
    onUp(index) {
        this.moveTag('up', index);
    }
    onDown(index) {
        this.moveTag('down', index);
    }
    saveTag() {
        let saveList = this.tagList.filter((item) => {
            return item.name && item.id;
        });
        let type = 'tagList';
        this.$store.dispatch('updateSchoolInfo', {
            schoolId: this.schoolInfo.id,
