        let upIndex = 0;
        if (state === 'up') {
            upIndex = index - 1;
        }
        else {
            upIndex = index + 1;
        }
        let currentItem = deepClone(this.tagList[index]);
        let upItem = deepClone(this.tagList[upIndex]);
        this.$set(this.tagList, index, upItem);
        this.$set(this.tagList, upIndex, currentItem);
    }
