import { __decorate } from "tslib";
import { Component, Vue } from 'vue-property-decorator';
import page from '@/components/common-components/page.vue';
import pageView from '@/components/common-ui/page-view.vue';
import rightBox from '@/components/common-components/right-box.vue';
import editBox from '@/components/common-components/edit-box.vue';
import addTag from '@/views/schoolAdmin/businessManagement/tagAdmin/index.vue';
let businessManagement = class businessManagement extends Vue {
    constructor() {
        super(...arguments);
        this.className = '商家管理';
        this.shopList = [];
        this.showMore = false;
        // 添加标签
        this.showEditbox = false;
        this.searchForm = {
            title: '',
            page: 1,
            schoolId: this.schoolInfo.id
        };
        this.oprateConfig = [{
                text: '标签',
                action: 'tag'
            }, {
                text: '添加',
                action: 'add'
            }];
    }
    get schoolInfo() {
        return this.$store.state.schoolInfo;
    }
    getSchoolShopList() {
        this.$store.dispatch('getSchoolShopList', this.searchForm).then((e) => {
            if (e.success) {
                let setArr = [];
                if (e.data.length < 20) {
                    this.showMore = false;
                }
                else {
                    this.showMore = true;
                }
                let resData = this.filterData(e.data);
                if (this.searchForm.page === 1) {
                    setArr = resData;
                }
                else {
                    setArr = [this.shopList, ...resData];
                }
                this.$set(this, 'shopList', setArr);
            }
        });
    }
    filterData(list) {
        let currentList = list.map((item, index) => {
            let returnItem = item;
            returnItem.open = !item.close;
            return returnItem;
        });
        return currentList;
    }
    mounted() {
        this.getSchoolShopList();
    }
    openChange(openState, shopId) {
        let tipText = openState ? '开启成功' : '关闭成功';
        this.$store.dispatch('updateShopInfo', {
            shopId,
            type: 'close',
            value: !openState
        }).then((e) => {
            if (e.success) {
                this.showToastType(tipText);
            }
        });
    }
    showToastType(text) {
        const toast = this.$createToast({
            txt: text,
            type: 'correct'
        });
        toast.show();
    }
    boxClick(action) {
        if (action === 'tag') {
            // this.$router.push({
            //   name: 'businessTags'
            // })
            this.showEditbox = true;
        }
    }
    moreClick() {
        this.searchForm.page++;
        this.getSchoolShopList();
    }
    toMerchantStaff(shopItem) {
        this.$router.push({
            name: 'merchantStaff',
            query: {
                shopId: shopItem.id,
                shopName: shopItem.title
            }
        });
    }
};
businessManagement = __decorate([
    Component({
        components: {
            page,
            pageView,
            rightBox,
            editBox,
            addTag
        }
    })
], businessManagement);
export default businessManagement;
